<template>
    <div class="txBind">
        <c-top-back></c-top-back>
        <r-group>
            <r-cell title="" value="15210903258"></r-cell>
            <r-input title="" :max="6" v-model="yzm" class="weui_vcode" placeholder="">
                <r-button slot="right" type="primary" class="sendYzmBtn" :disabled="sendYzmBtn"
                          @click.native="sendCodeBtn">{{txt}}
                </r-button>
            </r-input>
        </r-group>
        <div class="agreement"><label><input type="radio" value="1" v-model="radioValue"/></label><a
                href="javascript:;" @click="agreementClick"></a></div>
        <div class="btn">
            <r-button type="primary" text="" @click.native="bindBtnClick" :disabled="btnDisabled"></r-button>
        </div>
        <r-popup v-model="agreement" >
            <div class="agreementCon" :style="{height:h}">
                <div class="desc">
                    
                    <p>1.</p>
                    <p>2.</p>
                    <p>3.</p>
                    <p>4.,</p>
                    <p>5.</p>
                    <p>6./</p>
                    <p>7. </p>
                    <p>8.</p>
                    <p>9.()</p>
                    <p>10.</p>
                    <p>11.</p>
                    <p>12., </p>
                    <p>13.</p>
                    <p>14. </p>
                    <p>15.</p>
                </div>
                <div class="submit">
                    <r-button type="primary" @click.native="agreement=false;radioValue=1"></r-button>
                </div>
            </div>
        </r-popup>
    </div>
</template>

<script>
    import utils from '../../libs/utils.js';
    export default{
        data(){
            return {
                yzm: "",
                sendYzmBtn: false,
                txt: "",
                btnDisabled: true,
                radioValue: "",
                agreement: false,
            }
        },
        computed:{
            h(){
                return $(window).height()+"px";
            }
        },
        mounted: function () {
            this.$nextTick(function () {

            })
        },
        methods: {
            sendCodeBtn(){
                let that = this;
                var second = 59;
                that.sendYzmBtn = true;
                utils.ajax({
                    url: "/seller/seller/verify",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.txt = "(" + second + ")";
                            var timer = setInterval(function () {
                                if (second > 1) {
                                    second--;
                                    that.txt = "(" + second + ")";
                                } else {
                                    clearInterval(timer);
                                    that.txt = '';
                                    that.sendYzmBtn = false;
                                }
                            }, 1000);
                        } else {
                            that.sendYzmBtn = false;
                            that.$vux.alert.show(res.message);
                        }
                    }
                });

            },
            bindBtnClick(){
                let that = this;
                utils.ajax({
                    url: "/1",
                    success: function (res) {
                        if (res.code == "SUCCESS") {
                            that.$vux.toast.show('');
                            setTimeout(function () {
                                //
                            }, 2000)
                        } else {
                            that.$vux.alert.show(res.message);
                        }
                    }
                });
            },
            //
            agreementClick(){
                this.agreement = true;
            }
        },
        watch: {
            yzm(val, oldVal){
                if (val.length == 6 && this.radioValue == 1) {
                    this.btnDisabled = false;
                } else {
                    this.btnDisabled = true;
                }
            },
            radioValue(val, oldVal){
                if (this.yzm.length == 6 && val == 1) {
                    this.btnDisabled = false;
                } else {
                    this.btnDisabled = true;
                }
            }
        },
        components: {
            "cTopBack": require('../../components/x-top-back/x-top-back.vue'),
            "rCell": require("../../components/cell/cell.vue"),
            "rGroup": require("../../components/group/group.vue"),
            "rInput": require("../../components/input/input.vue"),
            "rButton": require("../../components/button/button.vue"),
            "rPopup": require("../../components/popup/popup.vue"),
        }
    }
    //require("../../assets/styles/views/batchExport.less");
</script>
<style lang="less">
    @import "../../assets/styles/views/personal";
</style>



